import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserRegistrationService } from '../user-registration.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-intermediatelogin',
  templateUrl: './intermediatelogin.component.html',
  styleUrls: ['./intermediatelogin.component.css']
})
export class IntermediateloginComponent implements OnInit {

  constructor(private router: Router,private service: UserRegistrationService) {
    let response=this.service.userData();
    response.subscribe((data)=>this.credentials=data);
   }
   credentials:any;
   namemain;
  ngOnInit() {
  }
  onAddPost(form:NgForm){
    if(form.invalid){
      console.log("Invalid");
      return;
    }
    const post = {pssword:form.value.password, email_id:form.value.email};
    console.log(post);
  }
  onLogin(form)
  {
    let flag:number=0;
    let email:String=form.value.email;
    let password:String=form.value.password;
    // console.log(email);
    // console.log(password);
    console.log(form.valid);
    for(let i in this.credentials)
    {
      // console.log(this.credentials[i].eMail)
      // console.log(this.credentials[i].password)
      
      if(email==this.credentials[i].eMail&&password==this.credentials[i].password)
      {
        if(this.credentials[i].plan=="Intermediate"){
        flag=1;
        // this.sendname.emit(this.credentials[i].firstName);
        this.router.navigateByUrl('intermediate');
        }
        else{
          flag=-1;
        }
      }
      else{

      }
    }
    if(flag==0)
    {
      alert("Invalid Credentials");
    }
    else if(flag==-1){
      alert("You do not have an intermediate membership");
    }
  }

}
